.header-list {
    margin-top: 100px;
    display: grid;
    justify-content: center;
    grid-template-columns: 200px 120px 60px 280px 200px ;
    grid-template-rows: 36px 32px auto 150px;
    grid-gap: 10px;
}

.header:nth-of-type(1) {
    grid-column: 1 / span 1;
    grid-row: 1 / span 3;
}
.header:nth-of-type(2) {
    grid-column: 2 / span 3;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
.header:nth-of-type(3) {
    grid-column: 5 / span 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header:nth-of-type(4) {
    grid-column: 2 / span 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header:nth-of-type(5) {
    grid-column: 3 / span 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header:nth-of-type(6) {
    grid-column: 2 / span 4;
}
.header:nth-of-type(7) {
    grid-column: 1 / span 5;
    display: grid;
    display: grid;
    justify-content: center;
    grid-template-columns: 140px 140px 140px 140px 140px;
    grid-gap: 20px;
}
.header__history:nth-of-type(1) {
    height: 90px;
    width: 90px;
    border-radius: 50%;
    overflow: hidden;
}
.header__history:nth-of-type(2) {
    height: 90px;
    width: 90px;
    border-radius: 50%;
    overflow: hidden;
}
.header__history:nth-of-type(3) {
    height: 90px;
    width: 90px;
    border-radius: 50%;
    overflow: hidden;
}
.header__history:nth-of-type(4) {
    height: 90px;
    width: 90px;
    border-radius: 50%;
    overflow: hidden;
}
.header__history:nth-of-type(5) {
    height: 90px;
    width: 90px;
    border-radius: 50%;
    overflow: hidden;
}

.header__figure {
    margin: 0;
    border-radius: 50%;
    height: 140px;
    width: 140px;
    display: flex;
    justify-content: center;
    overflow: hidden;
}

.header__figure img {
    height: 140px;
    width: 140px;
    object-fit: cover;
}
.header__history img {
    height: 90px;
    width: 90px;
    object-fit: cover;
}

.header h1 {
    margin: 0;
}
.header a  {
    text-decoration: none;
    font-weight: 700;
    font-size: 16px;
    color: white;
    margin-top: 6px;
}
.header p {
    line-height: 40px;
}
.header__options {
    margin-left: 12px;
    padding: 2px 14px;
    border: 1px solid lightslategray;
    border-radius: 8px;
    background: rgb(33, 161, 204);
}
.header__options a {
    /* height: 32px; */
    font-size: 16px;
}
.header__follow {
    border: 1px solid lightslategray;
    border-radius: 4px;
    padding: 2px 34px;
    background-color: rgb(33, 161, 204);
}
.header__follow a {
    font-size: 14px;
}
.header__more {
    width: 20px;
    border: 1px solid lightslategray;
    border-radius: 4px;
    padding: 2px 8px;
    background-color: rgb(33, 161, 204);
    text-align: center;
}


@media only screen and (max-width: 479px) {

    .header h1 {
        font-size: 26px;
        font-weight: 500;
    }

    .header a  {
        font-size: 10px;
        font-weight: 500;
    }

    .header p {
        font-size: 14px;
        line-height: 20px;
    }

    .header__options {
        padding: 3px 10px 0px 10px;
    }

    .header__follow {
        padding: 4px 12px;
    }

    .header__more {
        padding: 4px 6px;
    }

    .header-list {
        grid-template-columns: 85px 65px 40px 10px 55px;
        grid-template-rows: 28px 28px auto 90px;
    }

    .header:nth-of-type(6) {
        grid-column: 1 / span 5;
        grid-row: 3;
    }

    .header:nth-of-type(7) {
        margin-left: 55px;
        grid-template-columns: 70px 70px 70px 70px 70px;
        grid-row: 4;
        grid-gap: 0;
    }
        
    .header__figure {
        height: 80px;
        width: 80px;
    }

    .header__figure img {
        height: 80px;
        width: 80px;
    }

    .header__history:nth-of-type(1) {
        height: 60px;
        width: 60px;
    }
    .header__history:nth-of-type(2) {
        height: 60px;
        width: 60px;
    }
    .header__history:nth-of-type(3) {
        height: 60px;
        width: 60px;
    }
    .header__history:nth-of-type(4) {
        height: 60px;
        width: 60px;
    }
    .header__history:nth-of-type(5) {
        display: none;
        height: 60px;
        width: 60px;
    }

    .header__history img {
        height: 60px;
        width: 60px;
    }
}

@media only screen and (min-width: 480px) and (max-width: 767px) {

    .header h1 {
        margin-right: 12px;
        font-size: 32px;
        font-weight: 500;
    }
    .header p {
        font-size: 14px;
        line-height: 20px;
    }
    .header__options {
        padding: 4px 10px;
    }
    .header__follow {
        padding: 4px 12px;
    }
    .header__more {
        padding: 4px 6px;
    }

    .header-list {
        grid-template-columns: 110px 80px 40px 30px 120px;
    }

    .header:nth-of-type(6) {
        grid-column: 1 / span 5;
        grid-row: 3;
    }

    .header:nth-of-type(7) {
        grid-template-columns:  72px 72px 72px 72px 72px;
    }

    .header__history:nth-of-type(1) {
        height: 72px;
        width: 72px;
    }
    .header__history:nth-of-type(2) {
        height: 72px;
        width: 72px;
    }
    .header__history:nth-of-type(3) {
        height: 72px;
        width: 72px;
    }
    .header__history:nth-of-type(4) {
        height: 72px;
        width: 72px;
    }
    .header__history:nth-of-type(5) {
        height: 72px;
        width: 72px;
    }

    .header a {
        font-size: 14px;
    }
        
    .header__figure {
        height: 90px;
        width: 90px;
    }

    .header__figure img {
        height: 90px;
        width: 90px;
        object-fit: cover;
    }
    .header__history img {
        height: 90px;
        width: 90px;
    }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {

    .header-list {
        grid-template-columns: 180px 130px 50px 140px 160px;
    }

    .header:nth-of-type(7) {
        grid-template-columns: 110px 110px 110px 110px 110px;
    }
}