.post-list {
    margin-bottom: 100px;
    display: grid;
    grid-template-columns: repeat(3, 293px);
    justify-content: center;
    grid-gap: 28px;
}

.post {
    cursor: pointer;
    position: relative;
    display: block;
}

.post-images {
    margin: 0;
}

.post-images img {
    height: 293px;
    width: 100%;
    vertical-align: top;
    object-fit: cover;
}

.post-overlay {
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    display: none;
    align-items: center;
    justify-content: center;
    color: white;
    text-align: center;
}

.post:hover .post-overlay {
    display: flex;
}

.post-likes,
.post-comments{
    width: 80px;
    margin: 5px;
    font-weight: bold;
    text-align: center;
    display: inline-block;
}




@media only screen and (max-width: 479px) {

    .post-list {
        grid-template-columns: repeat(3, 100px);
        grid-gap: 3px;
    }

    .post-images img {
        height: 100px;
        object-fit: cover;
    }
}

@media only screen and (min-width: 480px) and (max-width: 767px) {
    
    .post-list {
        grid-template-columns: repeat(3, 152px);
        grid-gap: 8px;
    }

    .post-images img {
        height: 152px;
        object-fit: cover;
    }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
   
    .post-list {
        grid-template-columns: repeat(3, 230px);
        grid-gap: 20px;
    }

    .post-images img {
        height: 230px;
        object-fit: cover;
    }
}