.container__start {
    position: fixed;
    top: 0;
    z-index: 10;
    margin: 0;
    border-bottom: 1px solid lightgray;
    background-color: #ffffff;
    height: 54px;
    width: 100%;
    display: grid;
    grid-template-columns: 50px 65% 50px;
    align-items: center;
    justify-content: center;
}

.item__start {
    margin: 0;
    padding: 4px 16px;
    font-size: 24px;
    font-family: 'Sansita Swashed', cursive;
}

.item__start p {
    margin: 0;
}

.item__start i {
    color: rgba(0, 0, 0, 0.4);
}

.item__start i:hover {
    color: rgba(0, 0, 0, 0.9);
}



